const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:3000';

// Common fetch handler
async function handleRequest(url, options = {}) {
  try {
    const response = await fetch(url, {
      ...options,
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        ...options.headers
      },
      cache: 'no-store'
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(errorData.error || `HTTP error! status: ${response.status}`);
    }

    return await response.json();
  } catch (error) {
    console.error(`API Error (${url}):`, error);
    throw error;
  }
}

// Fetch products with filters
export async function fetchProducts({
  page = 1,
  limit = 10,
  sort = 'createdAt',
  order = 'desc',
  bestSellers,
  category,
  minPrice,
  maxPrice,
  search
} = {}) {
  const params = new URLSearchParams({
    page,
    limit,
    sort,
    order,
    ...(bestSellers && { bestSellers: 'true' }),
    ...(category && { category }),
    ...(minPrice && { minPrice }),
    ...(maxPrice && { maxPrice }),
    ...(search && { search })
  });

  return handleRequest(`${API_BASE_URL}/api/products?${params.toString()}`);
}

// Create new product
export async function createProduct(productData) {
  return handleRequest(`${API_BASE_URL}/api/products`, {
    method: 'POST',
    body: JSON.stringify(productData)
  });
}

// Update product
export async function updateProduct({ _id, ...updateData }) {
  return handleRequest(`${API_BASE_URL}/api/products`, {
    method: 'PUT',
    body: JSON.stringify({ _id, ...updateData })
  });
}

// Delete product
export async function deleteProduct(id) {
  return handleRequest(`${API_BASE_URL}/api/products?id=${id}`, {
    method: 'DELETE'
  });
}

// Get single product
export async function getProduct(id) {
  return handleRequest(`${API_BASE_URL}/api/products/${id}`);
}